.select {
    width: 100px;
    padding: 5px;
    padding-right: 30px;

    cursor: pointer;
    /*appearance: none;*/
    /*background-color: #003300;*/
    /*background-image: ;*/
    /*background-repeat: no-repeat, repeat;*/
    /*background-position: right 7em top 50%, 0 0;*/
    /*background-size: 65em auto, 100%;*/

    border: 2px inset rgb(118, 118, 118);
    color: #99ff99;
}

.select:focus {
    outline: none;
    border: #99ff99 solid 2px;
}

.option {
    color: #003300;
    background: #99ff99;
}

.option:checked {
    background: #003300;
    color: #99ff99;
}

.option:hover {
    box-shadow: 0 0 10px 100px #FED20F;
    transition: all 2s ease-in-out;
    background: red;
}